import $ from "jquery";

export function startJquery() {
  $(document).ready(function () {
    /* Section1*/
    $(".section1-bg").height($(window).height());
    $("#section1").height($(".section1-bg").height());
    $("#section5").height($(window).height());
    $("#section6").height($(window).height());

    $("#section1")
      .find(".heading")
      .height(
        $(".section1-bg").height() -
          ($("#section1").find(".navbar").innerHeight() +
            $("#section1").find(".triangle-bottom-right").innerHeight())
      );

    //
    $("#section1")
      .find(".heading")
      .find("p")
      .width($("#section1").find(".heading").find("h1").width() / 1.5);
    /* section 5*/
    $("#section5")
      .find(".heading")
      .height(
        $(window).height() - $("#section5").find(".process").outerHeight(true)
      );
    /* animation o Scroll */
    var $animation_elements = $(".animation-element");
    var $window = $(window);
    $window.on("scroll resize", check_if_in_view);
    $window.trigger("scroll"); //to check the scroll!
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;

      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;

        //check to see if this current container is within viewport
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        ) {
          $element.addClass("in-view");
        } else {
          $element.removeClass("in-view");
        }
      });
    }
  });
}
