import React from "react";
import NavBar from "./navbar";

const Section1 = () => {
  return (
    <section id="section1">
      <div className="section1-bg"></div>
      <NavBar />
      <div class="col-10 text-white heading d-flex align-items-center ml-5 pl-5">
        <div className=" pl-5">
          <h1 class="display-4 font-weight-bold animation-element slide-from-bottom-3s">
            Hungry much?
          </h1>
          <p class="my-4 text-secondary2 animation-element slide-from-bottom-5s">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod incididunt.
          </p>
          <div className="animation-element fade-in-3s">
            <a
              class="btn btn-outline-secondary font-weight-bold ml-2 px-4 py-2 mt-4 "
              href="#"
            >
              SEE OUR MENU
            </a>
          </div>
        </div>
      </div>
      <div className="col-12 py-5 triangle-bottom-right">
        <div className="d-flex justify-content-end align-items-end pr-5 h-100 text-light animation-element fade-in-8s">
          <a
            onClick={() => console.log("Fix this for scrolling")}
            className="d-flex align-items-center mr-5 pr-5"
          >
            <span className="font-weight-bold">Scroll down</span>
            <i class="bx bx-down-arrow-alt ml-3"></i>
          </a>
        </div>
      </div>
    </section>
  );
};

export default Section1;
