import React from "react";
import ChickenNuggets from "../image/chicken-nuggets.png";
const Section4 = () => {
  return (
    <section id="section4">
      <div class="row text-white m-0 px-5 mr-3 ml-5">
        <div class="col-md-5 d-flex align-items-center">
          <img
            class="bd-placeholder-img bd-placeholder-img-lg featurette-image img-fluid mx-auto animation-element roll-from-left "
            width="400"
            height="400"
            src={ChickenNuggets}
          />
        </div>
        <div class="col-md-7 our-menu">
          <span className="font-weight-bold animation-element slide-from-bottom-5s">
            Vegetable
          </span>
          <h5 className="display-4 animation-element slide-from-bottom-5s">
            Chicken Nuggets
          </h5>
          <p className="animation-element slide-from-bottom-5s">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, eiusmod
            tempor incididunt ut labore et dolore magna aliqua elit.
          </p>
          <div className="animation-element fade-in-3s">
            <a
              class="btn btn-outline-secondary font-weight-bold ml-2 px-4 py-2 mt-4 "
              href="#"
            >
              ADD TO BAG
            </a>
          </div>
        </div>
      </div>
      <div className="col-12 py-5 triangle-bottom-right mt-n4"></div>
    </section>
  );
};

export default Section4;
